.popup {
    visibility: hidden;
    opacity: 0;
    display: flex;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0,0,0,0.5);
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: visibility 0.4s linear, opacity 0.4s linear;
}